* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell,
    'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: calc(100vh - 60px);
}
.bg-button {
  height: 50px;
  width: 90px;
  font-size: 20px;
}
.bg-button:first-child {
  margin-right: 10px;
}

.field {
  display: flex;
  flex-direction: column;
  margin-right: 20px;
  align-items: center;
}

.timer {
  display: flex;
}

.value {
  font-size: 50px;
}

.label {
  font-size: 20px;
}

#datetime-picker {
  width: 250px;
  height: 40px;
  margin-right: 10px;
}
#datetime-picker ~ button {
  height: 40px;
  font-size: 20px;
  display: inline-flex;
  align-items: center;
}
.wraper {
  display: flex;
}
